const getDefaultStatus = (optionData) => {
  return {
    name: '', // 
    backgroundColor: 'transparent',
    type: 'pie',
    label: {
      opacity: 1,
      fontSize: 13,
      lineHeight: 20,
    },
    startAngle: -40, // [0, 360]
    clockwise: false, // 3d
    radius: ['20%', '50%'],
    center: ['50%', '50%'],
    data: optionData,
    itemStyle: {
      opacity: 0, //02d
    },
  };
};

export { color, getPie3D, getParametricEquation, getDefaultStatus };
