<template>
  <div
    class="chart-container"
    :style="`background: ${background}; width: ${width}px; height: ${height}px`"
  >
    <div class="chart" ref="chart"></div>
    <div class="bg"></div>
  </div>
</template>

<script>
  import * as echarts from 'echarts';
  import 'echarts-gl';
  import { color, getPie3D, getParametricEquation, getDefaultStatus } from './chart.js'; // 3d

  export default {
    name: 'ThreeDimPie',
    props: {
      data: {
        type: Array,
        default: [],
      },
      colors: {
        type: Array,
        default: [],
      },
      config: {
        type: Object,
        default: {},
      },
      showlegend: {
        type: Boolean,
        default: false,
      },
      background: {
        type: String,
        default: 'transparent',
      },
      width: {
        type: [Number, String],
        default: 400,
      },
      height: {
        type: [Number, String],
        default: 400,
      },
      alpha: {
        type: Number,
        default: 15,
      },
      opacity: {
        type: Number,
        default: 0.75,
      },
      distance: {
        type: Number,
        default: 200,
      },
      ratio: {
        type: Number,
        default: 0.8,
      },
    },
    data() {
      return {
        optionData: [],
        optionColors: [],
        statusChart: null,
        option: {},
        legend: false,
        maxValue: 0,
        alphaValue: 15,
      };
    },
    created() {
      //
    },
    mounted() {
      const that = this;
      const { colors, data, config, showlegend } = that;
      if (!data || data.length == 0) {
        return;
      } else {
        that.optionData = data;
        that.optionColors = colors?.length > 0 ? colors : color;
        that.legend = showlegend;
        data.map((item) => (item.value > that.maxValue ? (that.maxValue = item.value) : null));
        that.maxValue > 20 ? (that.maxValue = 20) : null;
        that.maxValue < 5 ? (that.maxValue = 5) : null;
        that.alphaValue = that.alpha;
        that.setLabel();
        that.initChart();
      }
      //
      window.onresize = () => {
        try {
          this.changeSize();
        } catch {
          //
        }
      };
    },
    methods: {
      // label
      setLabel() {
        const { optionColors } = this;
        this.optionData.forEach((item, index) => {
          item.itemStyle = {
            color: optionColors[index],
          };
          item.label = {
            normal: {
              show: true,
              color: optionColors[index],
              formatter: ['{b|{b}}', '{c|{c}}{b|}', '{d|{d}%}'].join('\n'), // \n
              rich: {
                b: {
                  color: '#fff',
                  lineHeight: 25,
                  align: 'left',
                },
                c: {
                  fontSize: 22,
                  color: '#fff',
                  textShadowColor: '#fefefe10',
                  textShadowOffsetX: 0,
                  textShadowOffsetY: 2,
                  textShadowBlur: 5,
                },
                d: {
                  color: optionColors[index],
                  align: 'left',
                },
              },
            },
          };
          item.labelLine = {
            normal: {
              lineStyle: {
                width: 1,
                color: 'rgba(255,255,255,0.7)',
              },
            },
          };
        });
      },
      // 
      initChart() {
        this.statusChart = echarts.init(this.$refs.chart);
        //  option, 3d, 
        this.option = getPie3D(this.optionData, this.ratio || 0.8, this.distance || 200, this.alpha || 15, this.maxValue || 15, this.opacity || 0.75);
        this.statusChart.setOption(this.option);
        // label2dlabelLine3dsetOption
        if (this.legend) {
          this.option.series.push(getDefaultStatus(this.optionData));
          this.statusChart.setOption(this.option);
        }
        this.bindListen(this.statusChart);
      },
      // 
      // optionNameoptionopiton
      bindListen(myChart, optionName = 'option') {
        let selectedIndex = '';
        let hoveredIndex = '';
        // 
        myChart.on('click', (params) => {
          //  option.series 
          const isSelected = !this[optionName].series[params.seriesIndex].pieStatus.selected;
          const isHovered = this[optionName].series[params.seriesIndex].pieStatus.hovered;
          const k = this[optionName].series[params.seriesIndex].pieStatus.k;
          const startRatio = this[optionName].series[params.seriesIndex].pieData.startRatio;
          const endRatio = this[optionName].series[params.seriesIndex].pieData.endRatio;
          //  option 
          if (selectedIndex !== '' && selectedIndex !== params.seriesIndex) {
            this[optionName].series[selectedIndex].parametricEquation = getParametricEquation(
              this[optionName].series[selectedIndex].pieData.startRatio,
              this[optionName].series[selectedIndex].pieData.endRatio,
              false,
              false,
              k,
              this[optionName].series[selectedIndex].pieData.value,
            );
            this[optionName].series[selectedIndex].pieStatus.selected = false;
          }
          // / option 
          this[optionName].series[params.seriesIndex].parametricEquation = getParametricEquation(
            startRatio,
            endRatio,
            isSelected,
            isHovered,
            k,
            this[optionName].series[params.seriesIndex].pieData.value,
          );
          this[optionName].series[params.seriesIndex].pieStatus.selected = isSelected;
          //  seriesIndex
          selectedIndex = isSelected ? params.seriesIndex : null;
          //  option
          myChart.setOption(this[optionName]);
        });
        //  mouseover
        myChart.on('mouseover', (params) => {
          // 
          let isSelected;
          let isHovered;
          let startRatio;
          let endRatio;
          let k;
          //  mouseover 
          if (hoveredIndex === params.seriesIndex) {
            // 
          } else {
            //  option 
            if (hoveredIndex !== '') {
              //  option.series  false
              isSelected = this[optionName].series[hoveredIndex].pieStatus.selected;
              isHovered = false;
              startRatio = this[optionName].series[hoveredIndex].pieData.startRatio;
              endRatio = this[optionName].series[hoveredIndex].pieData.endRatio;
              k = this[optionName].series[hoveredIndex].pieStatus.k;
              //  option 
              this[optionName].series[hoveredIndex].parametricEquation = getParametricEquation(
                startRatio,
                endRatio,
                isSelected,
                isHovered,
                k,
                this[optionName].series[hoveredIndex].pieData.value,
              );
              this[optionName].series[hoveredIndex].pieStatus.hovered = isHovered;
              //  seriesIndex 
              hoveredIndex = '';
            }
            //  mouseover  option 
            if (params.seriesName !== 'mouseoutSeries' && params.seriesName !== 'pie2d') {
              //  option.series  true
              isSelected = this[optionName].series[params.seriesIndex].pieStatus.selected;
              isHovered = true;
              startRatio = this[optionName].series[params.seriesIndex].pieData.startRatio;
              endRatio = this[optionName].series[params.seriesIndex].pieData.endRatio;
              k = this[optionName].series[params.seriesIndex].pieStatus.k;
              //  option 
              this[optionName].series[params.seriesIndex].parametricEquation =
                getParametricEquation(
                  startRatio,
                  endRatio,
                  isSelected,
                  isHovered,
                  k,
                  this[optionName].series[params.seriesIndex].pieData.value + 60,
                );
              this[optionName].series[params.seriesIndex].pieStatus.hovered = isHovered;
              //  seriesIndex
              hoveredIndex = params.seriesIndex;
            }
            //  option
            myChart.setOption(this[optionName]);
          }
        });
        //  bug
        myChart.on('globalout', () => {
          // 
          let isSelected;
          let isHovered;
          let startRatio;
          let endRatio;
          let k;
          if (hoveredIndex !== '') {
            //  option.series  true
            isSelected = this[optionName].series[hoveredIndex].pieStatus.selected;
            isHovered = false;
            k = this[optionName].series[hoveredIndex].pieStatus.k;
            startRatio = this[optionName].series[hoveredIndex].pieData.startRatio;
            endRatio = this[optionName].series[hoveredIndex].pieData.endRatio;
            //  option 
            this[optionName].series[hoveredIndex].parametricEquation = getParametricEquation(
              startRatio,
              endRatio,
              isSelected,
              isHovered,
              k,
              this[optionName].series[hoveredIndex].pieData.value,
            );
            this[optionName].series[hoveredIndex].pieStatus.hovered = isHovered;
            //  seriesIndex 
            hoveredIndex = '';
          }
          //  option
          myChart.setOption(this[optionName]);
        });
      },
      // 
      changeSize() {
        try {
          this.statusChart.resize();
        } catch {
          //
        }
      },
    },
  };
</script>

<style lang="less" scoped>
  .chart-container {
    position: relative;
    width: var(--width, 400px);
    height: var(--height, 400px);

    .chart,
    .bg {
      width: 100%;
      height: 100%;
    }

    .bg {
      position: absolute;
      z-index: 0;
      bottom: 50px;
      left: 50%;
      width: 180px;
      height: 73px;
      transform: translateX(-50%);
      background: transparent;
      background-size: 100% 100%;
    }
  }
</style>
